import utils from "./utils.js";

// 

export class Slider {
  /**
   * 
   *
   * @param {Object} el dom
   * @param {Number} min 
   * @param {Number} max 
   * @param {Number} step (0)
   * @param {Number} value (0)
   * @param {Object} callbacks created, start, change, update, end
   */
  constructor(el, min, max, step, value = 0, callbacks = {}) {
    const THIS = this;
    this.el = el;
    this.min = Number(min); // 
    this.max = Number(max); // 
    this.step = Number(step); // 
    this.value = Number(value) == NaN ? value : Number(value); // 
    this.callbacks = callbacks; // 

    this.track = this.el.querySelector(".slider-track"); // 
    this.bar = this.track.querySelector(".slider-bar"); // 
    this.thumbTrack = this.track.querySelector(".slider-thumb-track"); // 
    this.thumb = this.track.querySelector(".slider-thumb"); // 

    this.el.addEventListener(utils.nameMap.dragStart, function (event) {
      const e = event || window.event;
      // 
      let trackLength = THIS.track.offsetWidth;
      // 
      let nMax = THIS.thumbTrack.offsetWidth;
      nMax = nMax ? nMax : trackLength;
      // 
      let thumbTrackX = (trackLength - nMax) / 2;
      // X
      let clientX = e.clientX || e.changedTouches[0].clientX;
      // 
      let nLeft = Math.round(utils.getBoundingClientRectViewLeft(this));
      // 
      let nLength = clientX - nLeft - thumbTrackX;
      // 
      nLength = nLength >= nMax ? nMax : nLength <= 0 ? 0 : nLength;
      let value = THIS.step
        ? Math.round(((nLength / nMax) * (THIS.max - THIS.min)) / THIS.step) * THIS.step + THIS.min
        : (nLength / nMax) * (THIS.max - THIS.min) + THIS.min;
      if (THIS.callbacks.start) {
        THIS.callbacks.start(value);
      }
      // 
      if (THIS.value != value) {
        THIS.update(value);
      }

      let mousemoveEvent = function (event) {
        const e = event || window.event;
        // 
        e.preventDefault();
        e.stopPropagation();
        // X
        let clientX = e.clientX || e.changedTouches[0].clientX;
        // 
        let nLength = clientX - nLeft - thumbTrackX;
        // 
        nLength = nLength >= nMax ? nMax : nLength <= 0 ? 0 : nLength;

        let value = THIS.step
          ? Math.round(((nLength / nMax) * (THIS.max - THIS.min)) / THIS.step) * THIS.step + THIS.min
          : (nLength / nMax) * (THIS.max - THIS.min) + THIS.min;
        // 
        if (THIS.value != value) {
          THIS.update(value, true);
        }
        window.getSelection ? window.getSelection().removeAllRanges() : document.selection.empty();
      };

      let removeEvent = function (event) {
        const e = event || window.event;
        e.stopPropagation();
        window.getSelection ? window.getSelection().removeAllRanges() : document.selection.empty();
        document.removeEventListener(utils.nameMap.dragMove, mousemoveEvent);
        document.removeEventListener(utils.nameMap.dragEnd, removeEvent);
        if (THIS.callbacks.end) {
          THIS.callbacks.end(value);
        }
      };

      document.addEventListener(utils.nameMap.dragMove, mousemoveEvent);
      document.addEventListener(utils.nameMap.dragEnd, removeEvent);
    });

    // (this)
    if (this.callbacks.created) {
      this.callbacks.created(this);
    }
    // 
    setTimeout(() => {
      this.update(this.value, false);
    }, 0);
  }
  change(value, ...args) {
    // 
    this.value = value <= this.min ? this.min : value >= this.max ? this.max : value;
    // 
    // let per = (this.value - this.min) / (this.max - this.min);
    let per = (this.value - (this.min + 2)) / (this.max - this.min - 4);
    // 
    this.thumb.style.left = per * 100 + "%";
    // 
    this.bar.style.width = Math.max(Math.min(per, 1), 0.01) * 100 + "%";
    if (this.callbacks.change) {
      this.callbacks.change(value, ...args);
    }
  }
  update(value, ...args) {
    // 
    this.change(value, ...args);
    // 
    if (this.callbacks.update) {
      this.callbacks.update(value, ...args);
    }
  }
}

export class Slider_vertical {
  /**
   * 
   *
   * @param {Object} el dom
   * @param {Number} min 
   * @param {Number} max 
   * @param {Number} step (0
   * @param {Number} value (0)
   * @param {Object} callbacks created, start, change, update, end
   */
  constructor(el, min, max, step, value = 0, callbacks = {}) {
    const THIS = this;
    this.el = el;
    this.min = Number(min); // 
    this.max = Number(max); // 
    this.step = Number(step); // 
    this.value = Number(value) == NaN ? value : Number(value); // 
    this.callbacks = callbacks; // 

    this.track = this.el.querySelector(".slider-track");
    this.bar = this.track.querySelector(".slider-bar");
    this.thumbTrack = this.track.querySelector(".slider-thumb-track"); // 
    this.thumb = this.track.querySelector(".slider-thumb");

    this.el.addEventListener(utils.nameMap.dragStart, function (event) {
      const e = event || window.event;
      // 
      let trackLength = THIS.track.offsetHeight;
      // 
      let nMax = THIS.thumbTrack.offsetHeight;
      nMax = nMax ? nMax : trackLength;
      // 
      let thumbTrackY = (trackLength - nMax) / 2;
      // Y
      let clientY = e.clientY || e.changedTouches[0].clientY;
      // 
      let nTop = utils.getElementViewTop(this);
      // 
      let nLength = nMax - (clientY - nTop - thumbTrackY);
      // 
      nLength = nLength >= nMax ? nMax : nLength <= 0 ? 0 : nLength;
      let value = THIS.step
        ? Math.round(((nLength / nMax) * (THIS.max - THIS.min)) / THIS.step) * THIS.step + THIS.min
        : (nLength / nMax) * (THIS.max - THIS.min) + THIS.min;
      let per = (value - (THIS.min + 6)) / (THIS.max - THIS.min - 12);
      if (THIS.callbacks.start) {
        THIS.callbacks.start(value);
      }
      // 
      if (THIS.value != value) {
        THIS.update(value, true);
      }

      // 
      THIS.thumb.style.top = (1 - per) * 100 + "%";
      // 
      // THIS.bar.style.height = per * 100 + "%";
      THIS.bar.style.height = Math.max(Math.min(per, 1), 0) * 100 + "%";

      let mousemoveEvent = function (event) {
        const e = event || window.event;
        // 
        e.preventDefault();
        window.getSelection ? window.getSelection().removeAllRanges() : document.selection.empty();
        // Y
        let clientY = e.clientY || e.changedTouches[0].clientY;
        // 
        let nLength = nMax - (clientY - nTop - thumbTrackY);
        // 
        nLength = nLength >= nMax ? nMax : nLength <= 0 ? 0 : nLength;

        let value = THIS.step
          ? Math.round(((nLength / nMax) * (THIS.max - THIS.min)) / THIS.step) * THIS.step + THIS.min
          : (nLength / nMax) * (THIS.max - THIS.min) + THIS.min;
        // 
        if (THIS.value != value) {
          THIS.update(value, true);
        }
      };
      let removeEvent = function (event) {
        const e = event || window.event;
        e.preventDefault();

        document.removeEventListener(utils.nameMap.dragMove, mousemoveEvent);
        document.removeEventListener(utils.nameMap.dragEnd, removeEvent);
        if (THIS.callbacks.end) {
          THIS.callbacks.end(value);
        }
      };

      document.addEventListener(utils.nameMap.dragMove, mousemoveEvent);
      document.addEventListener(utils.nameMap.dragEnd, removeEvent);
    });
    // (this)
    if (this.callbacks.created) {
      this.callbacks.created(this);
    }
    // 
    setTimeout(() => {
      this.update(this.value, false);
    }, 0);
  }
  change(value, ...args) {
    // this.value = value <= this.min ? this.min : value >= this.max ? this.max : value;
    this.value = Math.max(Math.min(value, this.max), this.min);
    // 
    let per = (this.value - (this.min + 6)) / (this.max - this.min - 12);
    // 
    this.thumb.style.top = (1 - per) * 100 + "%";
    // 
    this.bar.style.height = Math.max(Math.min(per, 1), 0) * 100 + "%";
    if (this.callbacks.change) {
      this.callbacks.change(value, ...args);
    }
  }
  update(value, ...args) {
    this.change(value, ...args);
    // 
    if (this.callbacks.update) {
      this.callbacks.update(value, ...args);
    }
  }
}

export class Picker {
  /**
   * 
   *
   * @param {Object} group 
   * @param {Number} value (null)
   * @param {Object} callbacks created, change, pick
   */
  constructor(group, value = null, callbacks = {}) {
    const THIS = this;
    this.group = group; // 
    this.items = group.querySelectorAll(".picker-item"); // 
    this.value = value;
    this.callbacks = callbacks; // 
    this.valueList = [];
    this.items.forEach((item) => {
      this.valueList.push(item.getAttribute("data-value"));
      item.addEventListener("click", function () {
        THIS.pick(item.getAttribute("data-value"));
      });
    });
    // (this)
    if (this.callbacks.created) {
      this.callbacks.created(this);
    }
    setTimeout(() => {
      this.pick(this.value, "nonotice");
    }, 0);
  }
  change(value, ...args) {
    this.items.forEach((n, i) => {
      if (n.getAttribute("data-value") == value) {
        n.classList.add("picked");
      } else {
        n.classList.remove("picked");
      }
    });
    this.value = value;
    if (this.callbacks.change) {
      this.callbacks.change(value, ...args);
    }
  }
  pick(value, ...args) {
    this.change(value, ...args);
    if (this.callbacks.pick) {
      this.callbacks.pick(value, ...args);
    }
  }
}

export class MultiPicker {
  /**
   * 
   *
   * @param {Object} group 
   * @param {Number} value (null)
   * @param {Object} callbacks created, pick, unpick, update
   */
  constructor(group, value = [], callbacks = {}) {
    const THIS = this;
    this.group = group; // 
    this.items = group.querySelectorAll(".picker-item"); // 
    this.value = new Set();
    this.callbacks = callbacks; // 
    this.valueList = [];
    this.domMap = new Map();
    this.items.forEach((item) => {
      this.valueList.push(item.getAttribute("data-value"));
      this.domMap.set(item.getAttribute("data-value"), item);
      if (value.includes(item.getAttribute("data-value"))) {
        this.pick(item.getAttribute("data-value"), "nonotice");
      }
      item.addEventListener("click", function () {
        let val = this.getAttribute("data-value");
        if (THIS.value.has(val)) {
          THIS.unpick(val);
        } else {
          THIS.pick(val);
        }
      });
    });
    // (this)
    if (this.callbacks.created) {
      this.callbacks.created(this);
    }
    setTimeout(() => {
      this.pick(this.value, "nonotice");
    }, 0);
  }
  pick(val, ...args) {
    if (typeof val == "string") {
      this.domMap.get(val).classList.add("picked");
      this.value.add(val);
      if (this.callbacks.pick) {
        this.callbacks.pick(val, ...args);
      }
    } else if (typeof val == "array") {
      this.items.forEach((n) => {
        this.domMap.get(n).classList.add("picked");
        this.value.add(n);
        if (this.callbacks.pick) {
          this.callbacks.pick(n, ...args);
        }
      });
    }
    if (this.callbacks.update) {
      this.callbacks.update(this.value, ...args);
    }
  }
  unpick(val, ...args) {
    if (typeof val == "string") {
      this.domMap.get(val).classList.remove("picked");
      this.value.delete(val);
      if (this.callbacks.unpick) {
        this.callbacks.unpick(val, ...args);
      }
    } else if (typeof val == "array") {
      this.items.forEach((n) => {
        this.domMap.get(n).classList.remove("picked");
        this.value.delete(n);
        if (this.callbacks.unpick) {
          this.callbacks.unpick(n, ...args);
        }
      });
    }
    if (this.callbacks.update) {
      this.callbacks.update(this.value, ...args);
    }
  }
}

export class Switch {
  /**
   * 
   *
   * @param {Object} group 
   * @param {Boolean} value (false)
   * @param {Object} callbacks created, on, off, update
   */
  constructor(el, value = false, callbacks = {}) {
    const THIS = this;
    this.el = el;
    this.value = value;
    this.callbacks = callbacks; // 
    this.el.addEventListener("click", () => {
      this.update(!this.value);
    });
    // (this)
    if (this.callbacks.created) {
      this.callbacks.created(this);
    }
    setTimeout(() => {
      this.update(this.value, "nonotice");
    }, 0);
  }
  on(...args) {
    this.value = true;
    this.el.classList.add("switch-on");
    if (this.callbacks.on) {
      this.callbacks.on(...args);
    }
    if (this.callbacks.update) {
      this.callbacks.update(...args);
    }
  }
  off(...args) {
    this.value = false;
    this.el.classList.remove("switch-on");
    if (this.callbacks.off) {
      this.callbacks.off(...args);
    }
    if (this.callbacks.update) {
      this.callbacks.update(...args);
    }
  }
  update(value, ...args) {
    if (value) {
      this.on(...args);
    } else {
      this.off(...args);
    }
  }
}
