import defaultApiBackend from "./api.js";
import utils from "./utils.js";
export default (options) => {
  // default options
  const defaultOption = {
    container: options.element || document.getElementsByClassName("mfunsPlayer")[0],
    autoPlay: false, // 
    autoSwitch: false, // P
    draggable: true,
    blackBorder: true, // 
    theme: "#7b7ff7",
    loop: false, // 
    hotkey: true, // 
    userIsLogged: false,
    widescreen: false, // 
    widescreenSwitch: false, // 
    preload: "metadata",
    volume: 0.7,
    apiBackend: defaultApiBackend,
    video: [],
    contextmenu: [],
    mutex: true,
    activity: [],
    callback: {},
    pluginOptions: { hls: {}, flv: {}, dash: {}, webtorrent: {} },
    pickerColors: [
      "#FE0302",
      "#FFFF00",
      "#00CD00",
      "#00FF00",
      "#4E6EF2",
      "#89D5FF",
      "#7B7FF7",
      "#757575",
      "#FFFFFF",
      "#FB7229",
    ],
  };
  options = Object.assign(defaultOption, options);

  options.contextmenu = options.contextmenu.concat([
    {
      text: "",
      link: "https://www.mfuns.cn/author/7376",
    },
    {
      text: "",
      link: "https://www.mfuns.cn/author/6068",
    },
    {
      text: "",
      click: (player) => {
        player.infoPanel.toggle();
      },
    },
    {
      text: "",
      click: (player) => {
        player.videoColor.toggle();
      },
    },
    {
      text: "",
      click: (player) => {
        player.hotkey.toggle();
      },
    },
    {
      text: `mfunsPlayer v2.1.0`,
      link: "https://github.com/Mfuns-cn/mfunsPlayer",
    },
  ]);
  return options;
};
